@use './component';

@import 'normalize';
@import 'variables';

@import 'globalRules';
@import 'header';

/* Start Landing Section */
.landing{
    background-image: url("../images/landing.jpg");
    background-size: cover;
    height: calc(100vh - 64px);
    position: relative;
    .intro-text{
        position: absolute;
        left: 50%;
        top: 50%;
        text-align: center;
        width: 320px;
        max-width: 100%;
        transform: translate(-50%,-50%);
        h1{
            margin: 0;
            color: $main-color;
            font-size: 45px;
            font-weight: bold;
        }
        p{
            font-size: 19px;
        }
    }
}
/* End  Landing Section */

/* Start Features Section */
.features{
    padding-top: $section-py;
    padding-bottom: $section-py;
    background-color: $main-bgColor;

    .container{
        @include component.specialGrid;
        .feat {
            padding: 10px;
            text-align: center;

            i{
                color: $main-color;
            }
            h3{
                font-weight: 900;
                margin-top: $h3-my;
                margin-bottom: $h3-my;
            }
            p{
                @include component.specialParagraph;
            }
        }
    }
}
/* Start Features Section */

/* Start Services Section */
.services{
    padding-top: $section-py;
    padding-bottom: $section-py;
    .container{
        .special-heading{
            @include component.specialHeading;
        }
        .services-content{
            @include component.specialGrid;
            margin-top: 100px;
            .col{
                .srv{
                    display: flex;
                    margin-bottom: 40px;
                    i{
                        flex-basis: 60px;
                        color: $main-color;
                    }
                    .text {
                        flex: 1;
                        h3{
                            font-weight: 900;
                            margin: 0 0 20px;
                        }
                        p{  
                            @include component.specialParagraph;
                        }
                    }
                }
                .image{
                    text-align: center;
                    position: relative;
                    &::before{
                        content: "";
                        position: absolute;
                        background: $secondary-color;
                        width: 80px;
                        height: calc(100% + 80px);
                        right: 0;
                        top: -40px;
                        z-index: -1;
                    }
                    img{
                        max-width: $img-width;
                        width: 240px;
                    }
                }
            }
        }
    }
}

/* Small */
@media (max-width: 768px) {
    .services{
        .container{
            .special-heading{
                @include component.sm-specialHeading;
            }
            .services-content{
                .col{
                    .srv{
                        text-align: center;
                        flex-direction: column;
                        i{
                            color: red;
                        }
                    }
                }
            }
        }
    }
}

/* Large */
@media (max-width: 1199px) {
    .services{
        .container{
            .services-content{
                .col{
                    .image-column{
                        display: none
                    }
                }
            }
        }
    }
}
/* End  Services Section */

/* Start Portfolio Section */
.portfolio{
    padding-top: $section-py;
    padding-bottom: $section-py;
    background-color: $main-bgColor;
    .container{
        .special-heading{
            @include component.specialHeading;
        }
        .portfolio-content{
            @include component.specialGrid;
            margin-top: 100px;
            .card{
                background-color: #fff;
                img{
                    max-width: $img-width;
                }
                .info{
                    padding: 20px;

                    h3{
                        margin: 0;
                        font-weight: 900;
                    }
                    p {
                        @include component.specialParagraph;
                    }
                }
            }
        }
    }
}
/* End Portfolio Section */

/* Start About Section */
.about{
    padding-top: $section-py;
    padding-bottom: $section-py;
    .container{
        .special-heading{
            @include component.specialHeading;
        }
        .about-content{
            //@include component.specialGridAbout;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            margin-top: 100px;
            .image{
                position: relative;
                &::before{
                    content: "";
                    position: absolute;
                    background-color: $specialHeading-color;
                    z-index: -1;
                    width: 80px;
                    height: calc(100% + 40px);
                    left: -20px;
                    top: -24px;
                }
                &::after{
                    content: "";
                    position: absolute;
                    z-index: -1;
                    width: 80px;
                    height: calc(100% - 115px);
                    left: 195px;
                    top: -24px;
                    border-left: 60px solid $main-color;
                    border-bottom: 60px solid $main-color;
                }
                img{
                    max-width: $img-width;
                    width: 240px;
                }
            }
            .text{
                position: relative;
                flex-basis: calc(100% - 450px);
                p{
                    @include component.specialParagraph;
                    &:first-child{
                        margin: 0 auto 25px;
                        font-weight: 900;
                        color: #000;
                        font-size: 21px;
                    }
                    &:last-child{
                        padding-top: 15px;
                    }
                }
                hr{
                    border: 1px solid #10cab7;
                    width: 50%;
                    display: inline-block;
                }
            }
            
        }
    }
}
@media (max-width: 768px) {
    .about{
        .container{
            .about-content{
                flex-direction: column;
                text-align: center;
                .image{
                    margin-bottom: 20px;
                    &::before{
                        display: none
                    }
                    &::after{
                        display: none
                    }
                }
            }
        }
    }
}
/* End About Section */

/* Start Contact Section */
.contact{
    padding-top: $section-py;
    padding-bottom: $section-py + 40px;
    background-color: $main-bgColor;
    .container{
        .special-heading{
            @include component.specialHeading;
        }
        .info{
            margin-top: 100px;
            p.label{
                font-weight: 900;
                color: $secondary-color;
                font-size: 45px;
                margin: 0;
                letter-spacing: -2.5px;
            }
            a.link{
                display: inline-block;
                font-weight: 800;
                color: $main-color;
                font-size: 40px;
                text-decoration: none;
                padding: 30px;
            }
            .social{
                padding-left: 60px;
                @include component.specialParagraph;
                a{
                    margin-left: 20px;
                    text-decoration: none;
                    i{
                        color: $secondary-color;
                        transition: $transition;
                    }
                    &:hover{
                        i{
                            color: $main-color;
                        }
                    }
                }
            }
        }
    }
}
@media (max-width: 767px) {
    .contact{
        .container{
            .info{
                p.label{
                    font-size: 30px;
                }
                a.link{
                    text-decoration: none;
                    font-size: 25px;
                    padding: 15px;
                }
                .social{
                    padding-left: 30px;
                }
            }
        }
    }
}
/* End Contact Section */

@import 'footer';