header.header{
    padding: 20px;
    .container{
        display: flex;
        justify-content: space-between;
        align-items: center;
        img.logo{
            width: 60px;
        }
        .links{
            position: relative;
            &:hover{
                span.icon{
                    span{
                        &:nth-child(2) {
                            width: 100%;
                        }
                    }
                }
                ul{
                    display: block;
                }
            }
            span.icon{
                width: 30px;
                display: flex;
                flex-wrap: wrap;
                justify-content: flex-end;
                span{
                    height: 3px;
                    background-color: #222;
                    margin-bottom: 5px;
                    &:first-child {
                        width: 100%;
                    }
                    &:nth-child(2) {
                        width: 60%;
                        transition: $transition;
                    }
                    &:last-child {
                        width: 100%;
                    }
                }
            }
            ul{
                display: none;
                margin: 0;
                list-style-type: none;
                padding: 0;
                position: absolute;
                right: 0;
                top: calc(100% + 20px);
                background: #c3c3c3;
                min-width: 200px;
                z-index: 1;
                &::before {
                    content: "";
                    border-width: 10px;
                    border-style: solid;
                    border-color: transparent transparent #c3c3c3 transparent;
                    position: absolute;
                    right: 5px;
                    top: -20px;
                }
                li{
                    &:not(last-child){
                        a{
                            border-bottom: 1px solid #ddd;
                        }
                    }
                    a{
                        display: block;
                        padding: 15px;
                        text-decoration: none;
                        color: #333;
                        transition: $transition;
                        &:hover {
                            padding-left: 25px;
                            color: $main-color;
                        }
                    }
                }
            }
        }
    }
}