/* Paragraph */
$par-color: #777;
$par-lineHeight: 1.6;
$par-fontSize: 16px;

@mixin specialParagraph {
    line-height: $par-lineHeight;
    font-size: $par-fontSize;
    color: $par-color;
}

@mixin specialGrid {
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(300px, 1fr));
    gap: 10px;
}

@mixin specialGridAbout {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
}

/* Paragraph */
$specialHeading-color: #ebeced;

@mixin specialHeading {
    color: $specialHeading-color;
    font-size: 100px;
    margin: 0;
    text-align: center;
    letter-spacing: -2.5px;
    + {
        p {
            margin: -30px 0 0;
            font-size: 20px;
            text-align: center;
            color: #797979;
        }
    }
}

@mixin sm-specialHeading {
    font-size: 60px;
    + {
        p {
            margin-top: -20px;
        }
    }
}