/*
    m -> margin
    p -> padding
    t -> top
    b -> bottom
    l -> left
    r -> right
    x -> *-left and *-right
    y -> *-top and *-bottom
*/


/* Globals */
$main-color: #10cab7; // main color of SiteWeb
$main-bgColor: #f6f6f6; // main color of SiteWeb
$secondary-color: #2c4755; // Second color of SiteWeb
$section-py: 20px; // padding -top and -bottom
$section-mt: 100px; // margin top
$transition: .5s;


/* Container */
$container-px: 15px; // padding -left and -right


/* Header */
$haeder-color: #333; // main color of header
$specialHeading-color: #ebeced;


/* Heading 3 */
$h3-my: 20px; // padding -top and -bottom


/* Image */
$img-width: 100%; // Width Image

